import React, { useEffect } from "react";
// import { useDispatch } from "react-redux";
import cookie from "js-cookie";
import { AuthTokenKey, AuthTokenExpirationKey } from "../../../utills/constants";
// import { redirectTo } from "../../../redux/actions";
// import { RouteURLs } from "../../../routes/routes";
const Logout = () => {
  // const dispatch = useDispatch();
  useEffect(() => {
    localStorage.removeItem(AuthTokenKey);
    cookie.remove(AuthTokenKey, { path: '/', domain: process.env.REACT_APP_DOMAIN_NAME });
    cookie.remove(AuthTokenExpirationKey, { path: '/', domain: process.env.REACT_APP_DOMAIN_NAME });
    window.location.href = 'https://www.writearn.in/'
    // dispatch(
    //   redirectTo({
    //     path: RouteURLs.LOGIN,
    //   })
    // );
  });
  return <>Loading...</>;
};

export default Logout;
